$background-color: rgba(51, 51, 51, 0.05);
$text-color: #333;
$font-weight: 500;

.App {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $text-color;
  font-weight: $font-weight;
}

.login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;

  .profile-pic-div {
    display: flex;
    justify-content: center;
    
    .profile-pic {
      width: 10rem;
      height: 10rem;
    }
  }

  .image-upload-input {
    border-color: rgba(51, 51, 51, 0.01);
  }

  input, .image-upload-input {
    background: $background-color;
  }
  
  .login-button-div {
    display: flex;
    justify-content: center;

    .login-button {
        background-color: #ffffff;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 0.25rem;
        align-items: center;
        box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
        box-sizing: border-box;
        color: $text-color;
        cursor: pointer;
        display: inline-flex;
        justify-content: center;
        margin: 0;
        position: relative;
        text-decoration: none;
        transition: all 250ms;
        width: auto;
        font-family: system-ui, -apple-system, system-ui, "Helvetica Neue",
        Helvetica, Arial, sans-serif;
        font-size: 16px;
        font-weight: $font-weight;
        line-height: 20px;
        list-style: none;
        margin: 0;
        padding: 10px 12px;
        text-align: center;
        transition: all 200ms;
        vertical-align: baseline;
        white-space: nowrap;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
        }
    }

    .login-button:hover,
    .login-button:focus {
    border-color: rgba(0, 0, 0, 0.15);
    box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
    color: rgba(0, 0, 0, 0.65);
    }

    .login-button:hover {
    transform: translateY(-1px);
    }

    .login-button:active {
    background-color: #f0f0f1;
    border-color: rgba(0, 0, 0, 0.15);
    box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
    color: rgba(0, 0, 0, 0.65);
    transform: translateY(1px);
    }
}
  

.header {
  .navbar-top-row {
    display: flex;

    .search * {
      background: $background-color;

      .search-icon {
        background: none;
      }
    }
  }
}

.main {
  height: 100%;
  width: 100%;

  .event-item {
    border-radius: 0.5rem;
    padding: 0.5rem 1.5rem;

    .row * {
        padding: 0.1rem;
    }
  }

  .event-item:hover {
    background: $background-color;
  }
}

// Utility Class
.hidden {
    display: hidden;
}

.footer {
  // border: 1px solid orange;
  background: #fff;
}
